.brands-section {

  .container {
    max-height: 1200px;
    width: 100%;

    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: center;

    @include respond-to('laptop') {
      flex-direction: row;
      align-items: flex-start;
      gap: 90px;
    }
  }

  margin-bottom: 33px;

  @include respond-to('laptop') {
    margin-bottom: 133px;
  }
}



   img {
     margin: 0 auto;
   }